/*------------------------覆蓋設定開始----------------------------*/
/*bootstrap設定*/
.custom-checkbox .custom-control-input:checked ~ .custom-control-label::after{
  background-color: $primary-color;
  border-radius: .25rem;
}
.custom-radio .custom-control-input:checked ~ .custom-control-label::after {
  background-color: $primary-color;
  border-radius: 50%;
}
.dropdown-item.active, .dropdown-item:active{
  background: none;
}
.carousel{
  border-radius: 5px;
  overflow: hidden;
  border: 1px solid;
}
.form-group{
  @include mobile-lg{
    margin-bottom: 0.5rem;
  }
}
.container{
  max-width: 1300px;
}
.pagination{
  .page-link{
    border-radius: 0;
  }
  .page-item:first-child{
    .page-link{
   border-radius: 5px 0 0 5px;
   }
  }
  .page-item:last-child{
    .page-link{
   border-radius: 0 5px 5px 0;
   }
  }
}
.download-content{
  button{
    background: $white;
      text-decoration: none!important;
      display: flex;
      justify-content: space-between;
      color: $primary-color;
      border-radius: 0;
      border: 1px solid $primary-color;
      border-left: 5px solid $primary-color;
      border-bottom: 0; 
      &:hover{
        background: $calm-light-color;
        color: $primary-color;
      }
      &.active{
        border-bottom: 1px solid $primary-color;
        background: lighten($cold-color, 10%);
        color: $white;
      }
      &:focus{
        box-shadow: none;
      }
  }
  .card-body{
    border-right: 1px solid $primary-color;
    border-left: 1px solid $primary-color;
  }
}
/*popup設定*/
.mfp-wrap,.mfp-bg{
  z-index: 1060;
}
.mfp-img{
  border: 1px solid $primary-color;
  border-radius: 8px;
  padding: 0!important;
  margin: 40px auto!important;
}
.mfp-title{
  color: $primary-light-color;
  margin-top: 5px;
}
/*圖片設定*/
.img-sm{
  @include iPad{
    max-width: 150px;
  }
  @include mobile-lg{
    max-width: 100px;
  }
}
/*------------------------內頁設定開始----------------------------*/
.page-container{
    background: url("../images/bbg.jpg") $primary-light-color fixed;
    background-size: cover;
    padding: 152px 0 80px 0;
    min-height:calc(100vh - 201px);
    @include PC{
      padding: 110px 0 60px 0;
    }
    @include iPad{
      background: url("../images/bbg-mobile.jpg") fixed no-repeat left 70px #DCF0E7;
    background-size: 1100px auto;
    }
}


/*------------------------線上報名開始----------------------------*/
#slide-5 {
    background: url(../images/bg5.jpg) fixed;
    background-color: #E68820;
    color: #fff;
    padding-top: 130px;
  }
  #slide-5 label{
    font-weight: normal;
  }
  .signup .focus{
    font-size: 16px;
    line-height: 26px;
    color:#FFF988;
    text-shadow: 1px 1px 1px #547E87;
  }
  .signup .focus i{
    color:#FCE76D; 
  }
  .signup .focus span{
    font-size: 18px;
    font-weight: 400;
    color: #fff;
    font-family: 'Noto Sans TC', sans-serif;
  }
  .signup .form-horizontal label{
    text-align: right;
    margin-bottom: 0;
  }
  .signup .submit a, .signup button.search{
    width: 100%;
    border: none;
    color: #fff;
  }
  .signup .send,.signup button.search{
    background: #306FB4;
    color: #fff;
  }
  .signup .send:hover,.signup button.search:hover{
   background: #295F9A;
  }
  .signup .cancel{
   background: #666;
   color: #fff;
  }
  .signup .cancel:hover{
   background: #555;
  }
  .signup .alert {
    line-height: 1.6;
    margin-top: 30px;
    font-size: 13px;
    border: none;
    color: #fff;
    border: 1px solid #85BBBE;
    background: rgba(133,187,190,.3);
  }
  .signup .agree{
    margin-top: 30px;
  }
  .signup .agree button{display: inline-block;margin-bottom: 5px;}
  .signup .agree i{
    font-size: 1em;
  }
  .signup .card{
    background-color: #226A8D;
    border-radius: 0 0 8px 8px;
    border: none;
  }
  
  .form-horizontal .form-group{
    border-bottom: 1px solid #89B9BB;
    padding: 0 0 10px 0;
    margin-bottom: 10px;
  }
  .form-horizontal .form-group:last-child{
    border-bottom: none;
  }
  .form-group button:hover,.form-group button:focus{
    background: none;
    text-decoration: underline;
  }
  .form-group .well{
    color: #666;
    font-weight: bold;
    margin: 5px 0 0 0;
    padding: 10px;
    background: #FFE99B;
    border:none;
    font-size: 14px;
    border-radius: 5px;
  }
  .form-group .well .control-label{
    text-align: left;
    padding: 5px;
  }
  .form-group .well .point{
    color: #E05A5E;
    font-weight: bold;
  }
  .form-group .well hr{
    margin: 10px 0;
    border-bottom: 1px dashed #DD8140;
  }
  
  .form-group select{
    display: inline-block;
    padding: 5px 6px;
  }
  .form-group.en-name input{
    width: 130px;
    display: inline;
  }
  .form-group.phone input:first-child,.form-group.phone input:last-child{
    width: 60px;
  }
  .form-group.phone input{
    width: 100px;
    display: inline;
    padding: 5px 6px;
  }
  .form-group.add select{
    width: 80px;
    display: inline;
  }
  .form-group.add input{
    display: initial;
  }
  .form-group.add input:first-child{
    width: 50px;
  }
  .form-group.add input:last-child{
    width: 120px;
  }
  .conference{
    border-top: 1px solid #94B19B;
    padding-top:15px;
  }
  .id{
    font-weight: bold;
    line-height: 1.6;
    font-size: 15px;
    color: #333;
    max-width: 700px;
  }
  .id h5{
    color: #BB634D;
  }
  .id ol{
    padding-left: 0;
    color: #666;
  }
  
  .id ol>li {
    text-indent: -30px;
    margin-left: 30px;
    list-style: none;
  }
  .id ol>li>ol>li{
    text-indent: -15px;
    margin-left: 15px;
  }
  
  .search .focus{
    margin: 0 0 30px 0;
  }
  .search .form-group:nth-child(2){
    border-bottom: none;
  }
  .sinfo{
    font-size: 16px;
    line-height: 1.8;
  }
  
  .sinfo span:not(.badge){
   display: inline-block;
   background: #3F7C9A;
   color: #C9E9EB;
   margin: 5px 10px 5px 0;
   border-radius: 20px;
   padding: 0 10px;
  }
  .sinfo span.opening{
    color: #FFF477!important;
    background: #226A8D;
    border-radius: 5px;
  }
  .sinfo ul{
    padding-left: 30px;
    margin: 0 0 40px 0;
  }
  .sinfo span.day{
    padding: 0 8px;
    font-size: 14px;
  }
  #slide-5 .sinfo .control-label{
   text-align: left;
   color: #FCE76D;
  }
  .cancel-btn:focus{outline: none;box-shadow: none;}
  .cancel-btn{
    border: none;
    background: #74A543;
    margin-right: 5px;
  }
  .cancel-btn:hover{
    background: #6F9D40;
  }
  .cancel-btn.now{
    background: #DD5558;
  }
  .cancel-btn.now:hover{
    background: #D73539;
  }
  button.close {
    color: #666;
    text-shadow: none;
    opacity: .8;
    z-index: 1000;
  }
  .opening{
    color: #FFF477;
    border-radius: 5px;
  }
  .conference h5{
    margin: 20px 0 10px 0;
  }
  .conference label{
    text-align: left;
  }
  .conference .form-check{
    padding-left: 0;
  }
  .conference button{
    background: #666;
    padding: 8px;
    border-radius: 5px 5px 0 0;
    color: #fff;
  }
  .conference button:hover{
    background: #555;
    color: #fff;
  }
  .conference button.now{
    background:#226A8D;
    color: #FFF477;
    pointer-events: none;
  }
  .conference button.more{
    border-radius: 5px;
    padding: 0 15px;
    background: #B5E2FD;
    color: #226A8D;
    font-weight: bold;
  }
  .conference button.more:hover{
    background: #fff;
  }
  .conference button.more i{
    font-size: 15px;
    color: #226A8D;
  }
  .conference .form-check-input {
    position: absolute;
    margin-top: 13px;
    margin-left: 20px;
    z-index: 1000;
  }
  .conference .custom-checkbox{
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #26789F;
  }
  .conference .custom-checkbox.last{
    border-bottom: none;
  }
  .conference .know{
    margin-top: 5px;
    color: #CCE6F2;
  }
  .conference .know a{
    padding: 0 8px;
  }
  .custom-checkbox .day{
    background:#346D83;
    border:1px solid #6B9879;
    padding: 0 5px;
    font-weight: normal;
    font-size: 14px;
    border-radius: 5px;
    color:#CCE3EC;
  }
  .custom-checkbox .badge{
    margin: 0 5px;
    font-size: 14px;
    color: #555;
  }
  .end{
    background-color: #1D668C;
    background-color: rgba( 29,102, 140, .5 );
    color: #fff;
    font-size: 30px;
    padding: 30px;
    border-radius: 8px;
    margin: 0 0 50px 0;
    line-height: 1.3;
  }
  .end div{
    font-weight: normal;
    font-size: 17px;
    line-height: 1.2;
    margin-top: 10px;
  }