/*--------------------選單開始------------------*/
.navbar {
	transition: all 0.2s ease;
	.navbar-brand {
		width: 300px;
		height: 32px;
		background: url(../images/logo.svg) no-repeat;
		background-size: contain;
		@include text-indent;
		@include PC {
			width: 233px;
			height: 25px;
		}
		@include iPad{
			width: 220px;
		}
	}
	.nav-item {
		border-bottom: 1px solid lighten($primary-color, 5%);
		&:last-child {
			border-bottom: 0;
		}
	}
	.nav-link {
		height: auto;
			padding: 15px 20px !important;
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 0;
		border: 0;
		color: $primary-dark-color;
		&:hover{
			background: $primary-color;
			color: $white;
		}
	}
	&.now,&.top {
		background: $secondary-light-color;
	}
	.navbar-toggler:focus {
		box-shadow: none;
	}
}

/*------------------------footer開始----------------------------*/
.gotop {
	display: block;
	width: 57px;
	height: 37px;
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	right: 10px;
	bottom: 10px;
	padding: 0;
	background: url(../images/gotop.svg);
}
footer {
	.footer-logo{
		max-width: 100px;
		@include iPad{
			max-width: 60px;
		}
	}
}

/*------------------------內容區塊開始----------------------------*/

.rwd-table {
	overflow: hidden;
	min-width: 100%;
	tr:nth-of-type(2n) {
		background: darken($color: $gray, $amount: 5%);
	}
	tr {
		@include mobile-lg {
			background: #012d46;
		}
	}
	th,
	td {
		margin: 0.5em 1em;
		text-align: left;
		border-top: 1px solid $primary-color;
		@include mobile-lg {
			border: none;
			margin: 0 1em;
		}
	}
	th {
		background: $secondary-color;
		color: $primary-color;
		font-weight: 500;
		@include mobile-lg {
			display: none;
		}
	}
	td {
		background: none;
		color: $white;
		text-align: center;
		@include mobile-lg {
			display: block;
			text-align: left;
		}
		&:before {
			content: attr(data-th) " : ";
			display: none;
			@include mobile-lg {
				display: inline-block;
				color: $primary-color;
			}
		}
	}
}

/*-----------------------單元2開始----------------------------*/
.section2 {
	background: url(../images/bg2.jpg) fixed $gray;
	background-size: cover;
	@include PC-sm {
		background-repeat: no-repeat;
		background-size: 60%;
	}
	@include mobile-md {
		background-attachment: initial;
		background-size: 40%;
	}
	.section-title {
		color: $gray;
	}
	.section-content {
		color: $gray;
		font-weight: 400;
	}
	.section-line {
		.hr {
			background-color: $gray;
			margin-left: auto;
		}
	}
}

/*-----------------------單元3開始----------------------------*/
.section3 {
	background: url(../images/bg3.jpg) fixed $gray;
	background-size: cover;
	@include iPad {
		background-position: right top;
		background-size: 60%;
		background-repeat: no-repeat;
	}
	@include mobile-md {
		background-attachment: initial;
	}
	.section-title {
		color: $gray;
	}
	.section-line {
		.hr {
			background-color: $gray;
		}
	}
}

/*------------------------單元4開始----------------------------*/
.section4 {
	background: url(../images/bg5.jpg) fixed;
	background-size: cover;
	color: $primary-color;
	@include mobile-lg {
		background: url(../images/bg5-mobile.jpg) fixed;
		background-size: cover;
	}
	.section-line {
		.hr {
			background-color: $primary-color;
		}
	}
	.section-content {
		color: $white;
	}
	.section-subtitle {
		color: $primary-color;
	}
}
/*------------------------單元5開始----------------------------*/
.section5 {
	background: url(../images/bg4.jpg) fixed;
	background-size: cover;
	@include mobile-lg {
		background: url(../images/bg4-mobile.jpg) fixed;
		background-size: cover;
	}
	.section-title {
		color: $gray;
	}
	.section-line {
		.hr {
			background-color: $gray;
		}
	}
	.card {
		border: 1px solid $primary-color;
		margin-bottom: 25px;
		background: $white;
		&:hover {
			background: $primary-color;
			text-decoration: none;
			border: 5px solid $primary-color;
			i {
				color: $white !important;
			}
		}
		.card-body {
			padding: 8px 45px 8px 15px;
			color: $secondary-color;
			line-height: 1.3;
			i {
				position: absolute;
				right: 15px;
				top: 6px;
				font-size: 1.5em;
				color: $primary-color;
			}
		}
	}
}
