/* --------------顏色設定開始--------------- */
$primary-color:#F5A41D;
$primary-dark-color:#ee7300;
$secondary-color:#FDE123;
$secondary-light-color:#FFFCD7;
$tertiary-color:#8FC31F;
$tertiary-dark-color:#628613;
$tertiary-light-color:#bfdb82;
$assist-color:#5F4E00;
$assist-light-color:#7a6919;
$calm-color:#d89dda;
$calm-dark-color:#ae73af;
$calm-light-color:#f2d9f3;
$cold-color:#81d8e0;
$cold-dark-color:#469385;
$soft-color:#fabbbb;
$soft-light-color:#ffdcdc;
$soft-dark-color:#e48181;
$danger-color:#e2606d;
$steady-color:#9f9ee9;
$steady-dark-color:#777dd8;
$steady-light-color:#bfbef5;
$gray:#666;
$gray-light:#C6C6C6;
$gray-dark:#333;
$black:#000;

$primary-light-color:#D0EAE7;
$primary-xlight-color:#e6f8f6;

$danger-light-color:#F78177;
$danger-xlight-color:#ffedeb;
$white:#fff;


$color-themes: (
  //屬性: key, value
  'primary-color': $primary-color,
  'primary-dark-color': $primary-dark-color,
  'primary-light-color': $primary-light-color,
  'primary-xlight-color': $primary-xlight-color,
  'secondary-color': $secondary-color,
  'secondary-light-color': $secondary-light-color,
  'tertiary-color': $tertiary-color,
  'tertiary-dark-color': $tertiary-dark-color,
  'tertiary-light-color': $tertiary-light-color,
  'assist-color': $assist-color,
  'assist-light-color': $assist-light-color,
  'calm-color': $calm-color,
  'calm-dark-color': $calm-dark-color,
  'cold-color': $cold-color,
  'cold-dark-color': $cold-dark-color,
  'soft-color': $soft-color,
  'soft-light-color': $soft-light-color,
  'soft-dark-color': $soft-dark-color,
  'danger-color': $danger-color,
  'danger-light-color': $danger-light-color,
  'danger-xlight-color': $danger-xlight-color,
  'steady-color': $steady-color,
  'steady-light-color': $steady-light-color,
  'steady-dark-color': $steady-dark-color,
  'white': $white,
  'gray': $gray,
  'gray-light': $gray-light,
  'gray-dark': $gray-dark,
  'black': $black,
);
/* --------------字型設定開始--------------- */
$font-xxxs:.7em;
$font-xxs:.8em;
$font-xs:.9em;
$font-sm:1em;
$font-md:1.2em;
$font-lg:1.4em;
$font-xl:1.8em;
$font-xxl:2.5em;
$font-xxxl:5em;

$font-size-themes: (
  //屬性: key, value
  'font-xxxs': $font-xxxs,
  'font-xxs': $font-xxs,
  'font-xs': $font-xs,
  'font-sm': $font-sm,
  'font-md': $font-md,
  'font-lg': $font-lg,
  'font-xl': $font-xl,
  'font-xxl': $font-xxl,
  'font-xxxl': $font-xxxl,
);


$font-xlight:100;
$font-light:400;
$font-bold:500;
$font-heavy:600;
$font-xheavy:800;

$font-weight-themes: (
  //屬性: key, value
  'font-xlight': $font-xlight,
  'font-light': $font-light,
  'font-bold': $font-bold,
  'font-heavy': $font-heavy,
  'font-xheavy': $font-xheavy,
);


@mixin gradient-horizon($primary-color,$gray) {
background: $gray; /* Old browsers */
background: -moz-linear-gradient(left,  $primary-color 0%, $gray 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(left,  $primary-color 0%,$gray 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to right,  $primary-color 0%,$gray 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */

}

/* 字體設定 */
$font1: 微軟正黑體, Noto Sans TC, sans-serif;
$font2: Noto Sans TC, 微軟正黑體, sans-serif;
$font-en: arial, sans-serif;

/* --------------按鈕設定開始--------------- */

@mixin btn-dark-mix($bgcolor, $txt) {
  color: $txt;
  background: $bgcolor;
  &:hover {
      color: $txt;
      background: darken($color: $bgcolor, $amount: 5%);
  }
}
@mixin btn-light-mix($bgcolor, $txt) {
  color: $txt;
  background: $bgcolor;
  &:hover {
      color: $txt;
      background: lighten($color: $bgcolor, $amount: 5%);
  }
}
@mixin btn-border($color){
  color: $color;
  border: 1px solid $color;
  padding: 5px 15px;
  &:hover,&.active{
    color: $white;
    background: $color;
    border: 1px solid $color;
  }
}
@mixin btn-border-mix($color,$bdcolor){
  color: $color;
  border: 1px solid $bdcolor;
  padding: 5px 15px;
  background: $white;
  &:hover,&.active{
    color: $white;
    background: $bdcolor;
    border: 1px solid $bdcolor;
  }
}
@mixin btn-blank($color){
  color: $color;
  background: $white;
  padding: 5px 15px;
  text-align: center;
  &:hover{
    color: $white;
    background: $color;
  }
}
/*連結設定開始*/
@mixin txt-link-dark($color){
  color: $color;
  &:hover{
    color: darken($color: $color, $amount: 10%);
  }
}
@mixin txt-link-light($color){
  color: $color;
  &:hover{
    color: lighten($color: $color, $amount: 15%);
  }
}
/*標題設定開始*/
@mixin top-title($color) {
  font-size: $font-xl;
  letter-spacing: 8px;
  font-weight: 600;
  color:$color;
  position: relative;
  text-align: center;
  padding-bottom: 12px;
  margin-bottom: 30px;
  @include iPad{
    font-size:$font-lg;
    margin-bottom: 22px;
  }
  &:after{
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background: $color;
    left: 0;
    bottom: 0;
  }
}
/*表格設定開始*/
@mixin table-simple($bgcolor,$bdcolor){
  font-size: $font-sm;
  border: 2px solid $bdcolor;
  th{
      background: $bgcolor;
      border-bottom: 2px solid $bdcolor!important;
      color: $white;
      font-weight: $font-light;
      @include iPad{
        padding: .5rem;
      }
  }
  td{
    border-top: 1px solid $bdcolor;
    vertical-align: middle;
    @include iPad{
      padding: .5rem;
    }
  }
  td,th{
    border-color: $bdcolor;
  }
}