/*-----------------連結樣式---------------------*/
@each $key, $value in $color-themes {
    .link-#{$key} {
        color: $value;
        &:hover {
            color: lighten($value, 10%);
        }
    }
}
.link-shadow {
    &:hover {
        box-shadow: 0 0 10px $gray-light !important;
    }
}
/*-----------------文字樣式---------------------*/
@each $key, $value in $color-themes {
    //文字顏色
    .#{$key} {
        color: $value !important;
    }
}
@each $key, $value in $font-size-themes {
    //文字尺寸
    .#{$key} {
        font-size: $value !important;
    }
}
@each $key, $value in $font-size-themes {
    //文字尺寸
    .#{$key}-lg {
        @include PC {
            font-size: $value !important;
        }
    }
    .#{$key}-md {
        @include iPad {
            font-size: $value !important;
        }
    }
}
@each $key, $value in $font-weight-themes {
    //文字重量
    .#{$key} {
        font-weight: $value !important;
    }
}
.txt-shadow {
    text-shadow: 0 0 5px $gray;
}
/*-----------------背景樣式---------------------*/
@each $key, $value in $color-themes {
    .bg-#{$key} {
        background: $value !important;
    }
}
/*-----------------按鈕樣式---------------------*/
@each $key, $value in $color-themes {
    .btn-dark-#{$key} {
        @include btn-dark($value);
    }
}
.toggle-menu {
    .active {
        background: $secondary-color !important;
        border: 1px solid $secondary-color !important;
        pointer-events: none;
    }
}
.toggle-menu-top {
    button {
        border-radius: 15px 15px 0 0;
    }
    .active {
        background: $primary-color !important;
        border: 1px solid $primary-color !important;
        pointer-events: none;
    }
}
.play-btn {
    background: url("../images/icon-play.svg");
    width: 80px;
    height: 56px;
    position: absolute;
    left: calc(50% - 40px);
    top: calc(50% - 28px);
    transition: none;
    opacity: 0.6;
    @include text-indent;
    &-small {
        width: 61px;
        height: 43px;
        background-size: 61px;
    }
    &:hover {
        opacity: 1;
        background-position: bottom;
        transition: none;
    }
}
/*-----------------邊框樣式---------------------*/
@each $key, $value in $color-themes {
    .bd-#{$key} {
        border-color: $value !important;
    }
}
/*-----------------標題樣式---------------------*/
.main-title {
    display: inline-block;
    font-size: $font-xxl;
    font-weight: $font-heavy;
    position: relative;
    text-shadow: 2px 2px 2px #000;
    transform: skew(-20deg);
    @include PC {
        font-size: $font-xl;
    }
}
.main-title-info {
    display: inline-block;
    font-size: $font-lg;
    font-weight: $font-light;
    padding: 4px 24px;
    border-radius: 15px;
}
.subtitle {
    display: inline-block;
    margin: 0 auto 0 5px;
    background: $primary-xlight-color;
    border-left: 6px solid $primary-color;
    padding: 8px 24px;
    transform: skew(-10deg);
}
/*-----------------列表樣式---------------------*/
.txt-list {
    li {
        display: flex;
        align-items: start;
        .txt-badge {
            background: $gray-light;
            color: $white;
            border-radius: 8px;
            padding: 0 6px;
            margin: 2px 10px 0 0;
            font-size: $font-xs;
        }
    }
}
.txt-btn-list {
    li {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include iPad {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    .txt-btn-list-txt {
        width: calc(100% - 158px);
        @include iPad {
            margin-bottom: 10px;
            width: 100%;
        }
        &-en {
            width: calc(100% - 295px);
            @include PC {
                margin-bottom: 10px;
                width: 100%;
            }
        }
    }
}
.tag-list {
    li {
        border-bottom: 1px solid $primary-color;
        display: flex;
        padding: 20px 15px;
        align-items: flex-start;
        @include PC {
            flex-direction: column;
        }
        &:last-child {
            border-bottom: none;
        }
        .tag-list-tag {
            background: $secondary-light-color;
            color: $white;
            padding: 0 15px;
            border-radius: 4px 15px 15px 4px;
            margin-right: 15px;
            margin-left: -15px;
            font-size: $font-sm;
            @include PC {
                margin-bottom: 10px;
            }
        }
    }
}
.tag-list-header {
    border-radius: 30px 30px 0 0;
    background: $cold-color;
    margin-bottom: 0;
    padding: 10px;
}
.list-group-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-left-width: 5px !important;
    @include mobile-md{
        flex-direction: column;
        align-items: start;
        &.list-group-header{
            display: none;
        }
    }
    &:hover {
        background: $secondary-light-color;
    }
}
.list-badge{
    border-left: 1px solid $gray;
    padding: 0 0 0 20px;
    color: $assist-color;
    @include iPad{
        padding: 0 0 0 10px;
    }
}
.disabled {
    pointer-events: none !important;
}
/*-----------------區塊樣式---------------------*/
.info-box {
    background: $white;
    border-radius: 8px;
    border: 1px solid $primary-color;
    border-left: 6px solid $primary-color;
    border-right: 6px solid $primary-color;
    padding: 20px;
}

/*---------------------表格樣式---------------------*/
@each $key, $value in $color-themes {
    //文字顏色
    .table-bd-#{$key} {
        th,
        td {
            border-color: $value;
        }
    }
}
.table-rwd {
    min-width: 100%;
    th {
        display: table-cell;
        border-color: $white;
        @include iPad {
            display: none;
        }
    }
    td {
        display: table-cell;
        border-color: $cold-color;
        @include iPad {
            display: flex;
            flex-direction: column;
            text-align: left;
            display: block;
            border: 0 !important;
            padding: 10px 20px 10px 20px !important;
            position: relative;
            &:after {
                content: "";
                display: block;
                position: absolute;
                width: calc(100% - 40px);
                height: 1px;
                left: 20px;
                bottom: 0;
                background: $cold-color;
            }
            &.table-head:first-child {
                background: $cold-color;
                color: $white;
                font-weight: $font-light;
                &:before {
                    display: inline-block;
                    color: $white;
                    background: none;
                    padding: 0;
                    font-weight: $font-light;
                }
            }
            &:last-child {
                &:after {
                    display: none;
                }
            }
        }
        &:before {
            display: none;
            @include iPad {
                content: attr(data-th);
                display: inline-block;
                padding: 2px 10px;
                color: $white;
                font-size: $font-xs;
                background: lighten($cold-color, 15%);
                border-radius: 15px;
            }
        }
    }
}
.table-link {
    display: block;
    width: calc(100% + 1.5em);
    height: calc(100% + 1.5em);
    margin: -0.75em;
    padding: 0.75em;
    color: $gray-dark;
    border: 1px solid transparent;
    position: relative;
    &:after {
        position: absolute;
        content: "";
        width: 18px;
        height: 18px;
        background: url(../images/icon-plus.svg) no-repeat center;
        background-size: contain;
        right: 5px;
        bottom: 5px;
        opacity: 0.4;
    }
    &:hover {
        border: 1px solid $primary-color;
        background: $white;
        color: $gray-dark;
    }
}
.table-height {
    //讓td內容器可以高100%
    td {
        height: 1px;
    }
}
/*---------------------捲軸樣式---------------------*/
.scroll {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $white;
    }
    &::-webkit-scrollbar {
        width: 6px;
        height: 6px;
        background-color: $white;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: lighten($color: $gray, $amount: 15%);
    }
}
.scroll-light {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $white;
        border: 1px solid $gray-light;
        border-radius: 10px;
    }
    &::-webkit-scrollbar {
        width: 6px;
        height: 6px;
        background-color: $white;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: lighten($color: $gray-light, $amount: 15%);
    }
}
.scroll-border {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $tertiary-color;
        border-radius: 15px;
    }
    &::-webkit-scrollbar {
        width: 6px;
        height: 6px;
        background-color: $white;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: $white;
        border: 1px solid $tertiary-color;
    }
}
.scrollx-none {
    overflow-x: hidden;
}

.item_text {
    display: inline-block;
    background: $secondary-color;
    color: lighten($color: $primary-color, $amount: 15%);
    padding: 0px 10px;
    margin: 5px 10px 5px 0;
    border-radius: 5px;
}

.info-line {
    //線條小標
    display: flex;
    justify-content: space-between;
    position: relative;
    &::after {
        content: "";
        display: block;
        position: absolute;
        background: $gray-dark;
        width: 100%;
        height: 1px;
        left: 0;
        top: 12px;
        z-index: 50;
    }
    span {
        background: $white;
        position: relative;
        z-index: 100;
        padding: 0 5px;
    }
}
.article-title {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid $gray-dark;
    font-weight: $font-bold;
    padding-bottom: 15px;
    flex-wrap: wrap;
    @include PC {
        flex-direction: column-reverse;
        font-size: $font-xl;
    }
    @include PC {
        font-size: $font-lg;
        font-weight: $font-heavy;
        padding-bottom: 15px;
    }
}
.article-text {
    line-height: 1.8;
    margin-bottom: 35px;
}

.line-higher {
    line-height: 1.8;
    @include mobile-lg {
        line-height: 1.5;
    }
}
.map-title {
    background: lighten($color: $secondary-light-color, $amount: 25%);
    color: $primary-color;
    @include PC {
        font-size: $font-md;
    }
}
.media-title {
    @include mobile-lg {
        font-size: $font-xs;
    }
}
.modal-main-title {
    font-size: $font-lg;
    font-weight: $font-heavy;
}
.basic-title {
    font-size: $font-sm;
    font-weight: $font-heavy;
}
/*-----------------列表樣式---------------------*/
.icon-list {
    border-top: 1px solid $gray-dark;
    .icon-list-item {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid $gray-dark;
        padding: 8px 0;
        p {
            display: flex;
        }
        img {
            width: 30px;
            margin-right: 15px;
        }
    }
}
.news-list {
    border-top: 1px solid $gray-dark;
    .news-list-item {
        border-bottom: 1px solid $gray-dark;
        a {
            width: 100%;
            padding: 19px 0;
            display: flex;
        }
        P {
            width: calc(100% - 110px);
            @include over-text(1);
        }
    }
    .news-list-date {
        color: $primary-color;
        width: 110px;
    }
}
.mark-menu {
    border-bottom: 1px solid lighten($color: $secondary-light-color, $amount: 10%);
    margin-bottom: 25px;
    .mark-menu-item {
        border-right: 1px solid lighten($color: $secondary-light-color, $amount: 10%);
        &:first-child {
            a {
                border-radius: 8px 0 0 0;
            }
        }
        &:last-child {
            border-right: none;
            a {
                border-radius: 0 5px 0 0;
            }
        }
    }
    a {
        display: block;
        width: 100%;
        background: lighten($color: $secondary-light-color, $amount: 15%);
        color: $gray-dark;
        position: relative;
        padding: 13px 25px;
        font-size: $font-sm;
        &.active {
            background: $white;
            font-weight: $font-heavy;
            &::after {
                content: "";
                position: absolute;
                width: 100%;
                height: 3px;
                background: $primary-color;
                left: 0;
                bottom: 0;
            }
        }
    }
}
.article-menu {
    border-bottom: 7px solid $tertiary-color;
    a {
        font-weight: $font-bold;
        text-align: center;
        min-width: 140px;
        padding-left: 0;
        padding-right: 0;
    }
}
.text-list {
    padding-left: 25px;
    li {
        list-style-type: decimal;
    }
}
.step-list {
    display: flex;
    flex-wrap: wrap;
    li:nth-child(even) {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 10px;
    }
    .step-item {
        border: 1px solid $secondary-color;
        padding: 5px 20px;
        @include mobile-lg {
            padding: 4px 13px;
            font-size: $font-xs;
        }
    }
}
.align-btn-list {
    .align-btn {
        width: 100%;
        padding-left: 30%;
        @include PC {
            padding-left: 25%;
        }
        @include mobile-md {
            padding-left: 15%;
        }
        &:hover {
            img {
                -webkit-filter: invert(200%) sepia(4%) saturate(2%) hue-rotate(173deg) brightness(700%) contrast(100%);
                filter: invert(200%) sepia(4%) saturate(2%) hue-rotate(173deg) brightness(700%) contrast(100%);
            }
        }
    }
}
.basic-list {
    @include mobile-md {
        font-size: $font-xs;
        letter-spacing: normal;
    }
}

/* ---------------------頁數開始------------------------*/

.topic-hot-tag {
    li {
        display: inline-block;
        width: 180px;
        max-width: 180px;
        padding: 0 5px;
        margin: 0 0 10px 0;
        @include PC-md {
            width: 33.33%;
        }
        @include iPad {
            width: 50%;
        }
        a {
            display: block;
            width: 100%;
            @include iPad {
                padding: 5px 7px;
                font-size: $font-xxs;
                text-align: center;
            }
            span {
                color: $primary-color;
            }
            &:hover {
                span {
                    color: $white;
                }
            }
        }
        .topic-hot-tag-now {
            span {
                color: $white;
            }
        }
    }
    &-sm {
        li {
            width: 150px;
            max-width: 150px;
            @include PC-md {
                width: 33.33%;
            }
            @include iPad {
                width: 50%;
            }
        }
        a {
            text-align: center;
        }
    }
}
.topic-hot-link {
    li {
        display: inline-block;
    }
    a {
        font-size: $font-xs;
        @include iPad {
            font-size: $font-xxs;
        }
    }
}
/*-----------------區塊樣式---------------------*/
.event-card {
    box-shadow: 0 0 5px $gray-light;
    padding: 18px;
    &:hover {
        img {
            opacity: 0.5;
            transform: perspective(250px) translateZ(50px);
        }
        .event-card-img-box-text {
            opacity: 1;
            z-index: 100;
        }
    }
    .event-card-img-box-text {
        transition: all 0.2s ease;
        color: $white;
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        padding: 30px;
        pointer-events: none;
        p {
            position: absolute;
            width: 100%;
            left: 0;
            bottom: 25%;
            font-size: $font-xs;
            font-weight: $font-light;
            @include over_text(3);
            padding: 0 10%;
        }
    }
    .event-card-img-box {
        display: block;
        background: $black;
        position: relative;
        overflow: hidden;
        img {
            transition: all 0.2s ease;
        }
    }
    .event-card-title {
        font-size: $font-md;
        @include over_text(2);
    }
    .event-card-action {
        display: flex;
        justify-content: space-between;
        & > * {
            padding: 5px 25px;
        }
    }
    .event-card-info {
        @include mobile-lg {
            font-size: $font-xs;
            letter-spacing: normal;
        }
        span {
            color: $primary-color;
            font-weight: $font-bold;
            margin-right: 10px;
        }
    }
}
.news-card {
    padding: 30px;
    border: 1px solid $gray-light;
    @include iPad {
        padding: 20px;
    }
    &:hover {
        background: $white;
    }
    .news-card-title {
        font-size: $font-lg;
        font-weight: $font-heavy;
        line-height: 1.4;
        @include over_text(2);
        @include iPad {
            font-size: $font-md;
        }
    }
    .news-card-subtitle {
        font-size: $font-md;
        font-weight: $font-heavy;
        @include iPad {
            font-size: $font-sm;
        }
        @include mobile-lg {
            font-size: $font-xs;
        }
    }
    .news-card-info {
        display: flex;
        justify-content: space-between;
    }
    .news-card-txt {
        p {
            @include over_text(4);
            @include PC-md {
                @include over_text(3);
            }
            @include PC {
                @include over_text(2);
                font-size: $font-xs;
            }
        }
    }
}
.album {
    @include PC {
        height: auto;
    }
    .row {
        height: 100%;
    }
    .album-logo {
        width: 150px;
        @include iPad {
            margin: 15px auto;
        }
    }
    .album-box {
        display: block;
        position: relative;
        &:hover {
            opacity: 0.8;
        }
        .album-icon {
            position: absolute;
            right: 15px;
            bottom: 15px;
            font-size: $font-xl;
        }
    }
}
.topic-card {
    border-bottom: 1px solid $gray-dark;
    &-round {
        border-radius: 8px;
        padding: 18px;
        border: 1px solid $gray-light;
    }
    .topic-title {
        font-size: $font-md;
    }
    .topic-subtitle {
        font-size: $font-sm;
    }
    .topic-mark-box {
        display: flex;
        flex-wrap: wrap;
    }
    .topic-mark {
        color: $white;
        padding: 2px 4px;
        font-size: $font-xxs;
        text-align: center;
        margin-right: 1px;
        font-weight: $font-light;
    }
    .topic-hot-tag {
        font-size: $font-xs;
        li {
            @include mobile-lg {
                width: auto;
                margin: 0 2% 10px 2%;
            }
        }
    }
    .topic-info {
        font-weight: $font-light;
        @include over-text(2);
    }
}
.favorite {
    display: block;
    width: 20px;
    height: 19px;
    @include text-indent;
    &-lg {
        font-size: $font-lg;
    }
    &-line {
        background: url(../images/icon-heart-line.svg) no-repeat center;
        background-size: 100%;
    }
    &-solid {
        background: url(../images/icon-heart-solid.svg) no-repeat center;
        background-size: 100%;
    }
    &-delete {
        background: url(../images/icon-heart-solid-primary.svg) no-repeat center;
        background-size: 100%;
    }
}
.share {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    @include PC {
        width: 100%;
        margin-bottom: 15px;
    }
    a {
        display: block;
        width: 30px;
        height: 30px;
    }
    .favorite {
        height: 28px;
        & + span {
            font-size: $font-xxxs;
        }
    }
}
.mark-box {
    .mark-box-menu {
        display: flex;
        li:first-child {
            a {
                border-radius: 5px 5px 0 0;
            }
        }
        li {
            a {
                display: block;
                padding: 10px 30px 10px 30px;
                background: lighten($color: $secondary-color, $amount: 15%);
                color: $gray-dark;
                border-radius: 0 5px 0 0;
                font-weight: $font-bold;
                margin-top: 4px;
                @include iPad {
                    padding: 8px 20px 8px 20px;
                }
                @include mobile-lg {
                    padding: 8px 10px 8px 10px;
                    font-size: $font-xs;
                }
                &:hover {
                    color: $white;
                }
                &.active {
                    border-radius: 5px 5px 0 0;
                    background: $white;
                    padding: 14px 30px 10px 30px;
                    box-shadow: 0 -2px 3px $gray-light;
                    margin-top: 0;
                    color: $secondary-color;
                    @include iPad {
                        padding: 12px 20px 8px 20px;
                    }
                    @include mobile-lg {
                        padding: 12px 10px 8px 10px;
                    }
                    &:hover {
                        color: $secondary-color;
                    }
                }
            }
        }
    }
    .mark-box-content {
        background: $white;
        border-radius: 0 8px 8px 8px;
        box-shadow: 0 0 4px $gray-light;
        padding: 40px 8%;
    }
    label {
        font-weight: $font-bold;
    }
}
.slide-box-img {
    height: 550px;
    overflow: hidden;
    @include iPad {
        height: auto;
    }
}
.box-content {
    background: $white;
    border-radius: 8px;
    box-shadow: 0 0 4px $gray-light;
    padding: 0 8%;
}
.info-form {
    label,
    .label {
        width: 110px;
        font-weight: bold;
        justify-content: flex-start;
        @include iPad {
            width: 100%;
        }
    }
    .custom-control-label {
        width: auto;
    }
    input,
    select,
    textarea,
    .form-notice {
        width: calc(100% - 110px) !important;
        @include iPad {
            width: 100% !important;
        }
    }
    .form-check-input,
    .form-check-label {
        width: auto !important;
        font-weight: $font-bold;
    }
    .custom-control-label {
        font-weight: normal;
    }

    .custom-select {
        background: url(../images/icon-arrow-down-secondary.svg) no-repeat right top $white;
        &-short {
            width: 230px !important;
            @include PC {
                width: 160px !important;
            }
            @include iPad {
                width: 100% !important;
            }
        }
    }
    .form-control[readonly] {
        background-color: $white;
    }
}

.form-text-box {
    height: 200px;
    border: 1px solid lighten($color: $gray-light, $amount: 15%);
    @include mobile-lg {
        height: 300px;
    }
}
.form-notice {
    margin-left: 130px;
    @include iPad {
        margin-left: 0;
    }
}
.mobile-rounded {
    @include PC {
        border-radius: 10px;
    }
}
/*-----------------表單樣式---------------------*/
.inside-bar {
    //input內置按鈕
    button {
        padding: 5px;
        border: 1px solid $secondary-light-color;
        border-radius: 4px;
        width: 35px;
    }
}
.inside-bar-round {
    //input內置按鈕大圓角
    input {
        border-radius: 70px 0 0 70px;
    }
    button {
        padding: 3px 14px;
        border-radius: 0 70px 70px 0;
        width: 50px;
        height: 38px;
    }
}
.input-primary {
    border: 1px solid $primary-color;
    border-radius: 0;
    @include iPad {
        width: 100% !important;
    }
}
.input-secondary {
    border: 1px solid $secondary-color;
    border-radius: 0;
    @include iPad {
        width: 100% !important;
    }
}
.input-steady {
    border: 1px solid $tertiary-color;
    border-radius: 0;
    @include iPad {
        width: 100% !important;
    }
}
.input-over-light-base,
.input-over-light-base:focus {
    background-color: lighten($color: $secondary-light-color, $amount: 20%) !important;
    border-radius: 0;
    border: 0;
}
.custom-select {
    background: url(../images/icon-arrow-down.svg) no-repeat right top $white;
}
.round {
    //大圓角
    border-radius: 70px;
}
.fortime {
    background: url(../images/icon-time.svg) no-repeat right 2px $white !important;
    background-size: 50px;
    padding-right: 50px !important;
}
.fortime-secondary {
    background-image: url(../images/icon-time-secondary.svg);
    background-repeat: no-repeat;
    background-position: right top;
    background-size: 50px;
    padding-right: 50px !important;
}
.steady-checkbox {
    .custom-control-label::before {
        border: 1px solid $tertiary-color;
        border-radius: 0;
    }
    .custom-control-input:checked ~ .custom-control-label::after {
        border: 1px solid $tertiary-color;
        background-color: $tertiary-color;
    }
}

/*-----------------文字樣式---------------------*/
.small-txt {
    color: $gray;
    font-size: $font-xs;
}
.pattern-bg {
    background: url(../images/bg-left.png) no-repeat, url(../images/bg-right.png) no-repeat;
    background-position: left top, right top;
    background-size: 20%, 30%;
}
.letter-spacing-mobile-n {
    @include mobile-lg {
        letter-spacing: normal;
    }
}

/*-----------------連結樣式---------------------*/
.img-link {
    //圖片按鈕
    opacity: 1;
    transition: all 0.2s ease;
    &:hover {
        opacity: 0.8;
    }
}
.shadow-link {
    &:hover {
        box-shadow: none !important;
        transition: all 0.2s ease;
    }
}
.article-menu-select {
    div {
        background: $tertiary-color;
        color: $white;
        height: 38px;
        line-height: 38px;
        padding: 0 20px;
    }
    select {
        border: 1px solid $tertiary-color;
        border-radius: 0;
        min-width: 200px;
    }
}
/*-----------------flex樣式---------------------*/
.flex-unstrech {
    flex: 0 1 auto;
}
