/*------------------------首頁設定開始----------------------------*/
@keyframes obj1 {
  0% {
    transform: translate(0, -20px);
  }
  25% {
    transform: translateY(5px);
  }
  50% {
    transform: translateY(-10px);
  }
  75% {
    transform: translateY(3px);
  }
  100% {
    transform: translateY(-20px);
  }
}
@keyframes obj2 {
  0% {
    transform: translateY(5px);
  }
  25% {
    transform: translateY(-5px);
  }
  50% {
    transform: translateY(10px);
  }
  75% {
    transform: translateY(-3px);
  }
  100% {
    transform: translateY(5px);
  }
}
.banner-box {
  background: url(../images/main-bg.svg) no-repeat center 0,
    url(../images/index-bg.png) repeat-x fixed left top $secondary-color;
  background-size: 1920px, contain;
  padding-top: 64px;
  height: 100vh;
  @include PC-lg {
    background-size: 1800px, contain;
    height: auto;
  }
  @include iPad{
    background-position: center 120px,left top;
    background-size: 1000px, contain;
  }
}
.main {
  position: relative;
  height: 780px;
  @include PC-sm {
    height: 650px;
  }
  @include iPad {
    height: 550px;
  }
  @include mobile-lg {
    height: 950px;
  }
  & > * {
    transition: all 0.2s ease;
    position: absolute;
    @include PC-lg {
      zoom: 0.95;
    }
    @include PC {
      zoom: 0.9;
    }
    @include PC-sm {
      zoom: 0.75;
    }
    @include iPad {
      zoom: 0.6;
    }
  }
}
.title {
  left: calc(50% - 325px);
  top: -30px;
  width: 650px;
  z-index: 1;
  @include mobile-md {
    left: calc(50% - 250px);
    width: 500px;
  }
}
.scroll{
  left: calc(50% - 25px);
  bottom: -20px;
  width: 50px;
  opacity: 0.6;
  cursor: pointer;
  &:hover{
    opacity: 1;
  }
  @include iPad{
    display: none;
  }
}
.building {
  left: calc(50% - 250px);
  top: -5%;
  width: 500px;
  height: 679px;
  @include PC-lg {
    top: 2%;
  }
  @include PC-sm {
    top: 4%;
  }
  @include iPad {
    top: 13%;
  }
  @include mobile-lg {
    top: 25%;
  }
}
.build {
  right: 5%;
  top: 13%;
  width: 395px;
  height: 440px;
  animation: obj1 8s infinite;
  cursor: pointer;
  @include PC {
    top: 16%;
  }
  @include mobile-lg {
    top: 25%;
    z-index: 1;
  }
  @include mobile-md {
    right: 0;
  }
  &:hover {
    width: 415px;
    right: calc(5% - 10px);
  }
}
.motor {
  left: 3%;
  top: 0;
  width: 368px;
  height: 523px;
  animation: obj2 8s infinite;
  cursor: pointer;
  @include PC {
    top: 5%;
  }
  @include mobile-lg {
    top: 8%;
  }
  @include mobile-md {
    left: 0;
  }
  &:hover {
    width: 388px;
    left: calc(3% - 10px);
  }
}
.mechanical {
  left: 1%;
  top: 58%;
  width: 458px;
  height: 351px;
  z-index: 2;
  animation: obj2 8s infinite;
  cursor: pointer;
  @include PC-sm {
    top: 65%;
  }
  @include iPad {
    top: 65%;
  }
  @include mobile-lg {
    top: 64%;
  }
  &:hover {
    width: 478px;
    left: calc(1% - 10px);
  }
}
.digital {
  right: 0;
  top: 55%;
  width: 490px;
  height: 365px;
  animation: obj1 8s infinite;
  cursor: pointer;
  @include PC-sm {
    top: 58%;
  }
  @include iPad {
    top: 65%;
  }
  @include mobile-lg {
    top: 80%;
  }
  &:hover {
    width: 510px;
    right: -10px;
  }
}

.section {
  padding: 70px 0 90px 0;
  @include PC-sm {
    padding: 40px 0 60px 0;
  }
  &-build-bg {
    background: url(../images/bbg-line.svg),$secondary-color; /* Old browsers */
    background: url(../images/bbg-line.svg),-moz-linear-gradient(top, $secondary-color 0%, $secondary-light-color 100%); /* FF3.6-15 */
    background: url(../images/bbg-line.svg),-webkit-linear-gradient(top, $secondary-color 0%, $secondary-light-color 100%); /* Chrome10-25,Safari5.1-6 */
    background: url(../images/bbg-line.svg),linear-gradient(
      to bottom,
      $secondary-color 0%,
      $secondary-light-color 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    background-size: cover;
    background-attachment: fixed;
    @include iPad{
      background-size: 1500px;
    }
  }
  &-motor-bg {
    background: url(../images/bbg-line.svg),$secondary-light-color; /* Old browsers */
    background: url(../images/bbg-line.svg),-moz-linear-gradient(top, $secondary-light-color 20%, $cold-color 100%); /* FF3.6-15 */
    background: url(../images/bbg-line.svg),-webkit-linear-gradient(top, $secondary-light-color 20%, $cold-color 100%); /* Chrome10-25,Safari5.1-6 */
    background: url(../images/bbg-line.svg),linear-gradient(
      to bottom,
      $secondary-light-color 20%,
      $cold-color 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    background-size: cover;
    background-attachment: fixed;
    @include iPad{
      background-size: 1500px;
    }
  }
  &-digital-bg {
    background: url(../images/bbg-line.svg),$cold-color; /* Old browsers */
    background: url(../images/bbg-line.svg),-moz-linear-gradient(top, $cold-color 20%, $calm-light-color 100%); /* FF3.6-15 */
    background: url(../images/bbg-line.svg),-webkit-linear-gradient(top, $cold-color 20%, $calm-light-color 100%); /* Chrome10-25,Safari5.1-6 */
    background: url(../images/bbg-line.svg),linear-gradient(
      to bottom,
      $cold-color 20%,
      $calm-light-color 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    background-size: cover;
    background-attachment: fixed;
    @include iPad{
      background-size: 1500px;
    }
  }
  &-mechanical-bg {
    background: url(../images/bbg-line.svg),$calm-light-color; /* Old browsers */
    background: url(../images/bbg-line.svg),-moz-linear-gradient(top, $calm-light-color 20%, $soft-light-color 100%); /* FF3.6-15 */
    background: url(../images/bbg-line.svg),-webkit-linear-gradient(top, $calm-light-color 20%, $soft-light-color 100%); /* Chrome10-25,Safari5.1-6 */
    background: url(../images/bbg-line.svg),linear-gradient(
      to bottom,
      $calm-light-color 20%,
      $soft-light-color 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    background-size: cover;
    background-attachment: fixed;
    @include iPad{
      background-size: 1500px;
    }
  }
  &-map-bg {
    background: url(../images/bbg-line.svg),$tertiary-light-color; /* Old browsers */
    background: url(../images/bbg-line.svg),-moz-linear-gradient(top, $tertiary-light-color 20%, $secondary-light-color 100%); /* FF3.6-15 */
    background: url(../images/bbg-line.svg),-webkit-linear-gradient(top, $tertiary-light-color 20%, $secondary-light-color 100%); /* Chrome10-25,Safari5.1-6 */
    background: url(../images/bbg-line.svg),linear-gradient(
      to bottom,
      $tertiary-light-color 20%,
      $secondary-light-color 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    background-size: cover;
    background-attachment: fixed;
    @include iPad{
      background-size: 1500px;
    }
  }
  &-textbook-bg {
    background: url(../images/bbg-line.svg),$steady-light-color; /* Old browsers */
    background: url(../images/bbg-line.svg),-moz-linear-gradient(top, $steady-light-color 20%, $secondary-light-color 100%); /* FF3.6-15 */
    background: url(../images/bbg-line.svg),-webkit-linear-gradient(top, $steady-light-color 20%, $secondary-light-color 100%); /* Chrome10-25,Safari5.1-6 */
    background: url(../images/bbg-line.svg),linear-gradient(
      to bottom,
      $steady-light-color 20%,
      $secondary-light-color 100%
    ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    background-size: cover;
    background-attachment: fixed;
    @include iPad{
      background-size: 1500px;
    }
  }
}
.section-image{
  width: 375px;
  @include PC-lg{
    width: 320px;
  }
  @include PC{
    width: 290px;
  }
  @include PC-sm{
    width: 250px;
    margin-bottom: 20px;
  }
  @include iPad{
    width: 200px;
    margin-bottom: 10px;
  }
  @include mobile-lg{
    margin-bottom: 0;
  }
}
.slide-popup {
  display: block;
  position: relative;
  .slide-popup-title {
    position: absolute;
    display: block;
    width: 100%;
    background: $black;
    color: $white;
    opacity: 0.6;
    left: 0;
    bottom: 0;
    padding: 4px 16px;
  }
}
/*-----------------按鈕樣式---------------------*/
.btn-action{
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  @include iPad{
    justify-content: space-between;
    margin-bottom: 10px;
  }
  .btn-icon{
    width: 50px;
    margin-right: 15px;
    @include mobile-md{
      margin-right: 10px;
      width: 35px;
    }
  }
  .btn-icon-start{
    width: 75px;
    @include iPad{
      width: 60px;
    }
  }
}
/*-----------------圖片樣式---------------------*/
.text-round-img {
  max-width: 570px;
  margin: 0 25px 15px 0;
  @include iPad {
    max-width: 100%;
    height: auto;
  }
  img {
    max-width: inherit;
    width: 1000px;
    height: auto;
    @include iPad {
      width: 100%;
      height: auto;
    }
  }
}
/*-----------------區塊樣式---------------------*/
.dialogue-box {
  border-radius: 40px;
  background: rgba(255,255,255,0.6);
  position: relative;
  &::before {
      position: absolute;
      left: -80px;
      top: 150px;
      content: "";
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 0 80px 80px;
      border-color: transparent transparent rgba(255,255,255,0.6) transparent;
      @include PC-sm{
      border-width: 50px 0 0 50px;
      border-color: transparent transparent transparent rgba(255,255,255,0.6);
      left: auto;
      right: 100px;
      top: -50px;
      }
      @include iPad{
        border-width: 30px 0 0 30px;
        right: 30px;
        top: -30px;
      }
  }
  @include iPad{
    border-radius: 25px;
  }
}
.carousel-txt{
  background: rgba($gray, 0.9);
  position: absolute;
  color: $white;
  width: 30%;
  padding: 40px;
  right: 0;
  top: 0;
  height: 100%;
  @include PC{
    width: 100%;
    position: relative;
  }
}
.img-slider{
  position: relative;
  .carousel-control-next{
    right: 10px;
    @include iPad{
      right:-10px;
    }
    @include mobile-lg{
      right: 5px;
    }
  }
  .carousel-control-prev{
    left: 10px;
    @include iPad{
      left:-10px;
    }
    @include mobile-lg{
      left: 5px;
    }
  }
}
.img-slider-caption{
  .carousel-control-next, .carousel-control-prev{
    @include mobile-lg{
      -webkit-filter:brightness(5);
    }
  }
}

.carousel-control-next, .carousel-control-prev{
  color: $gray-dark;
  opacity: 0.5;
  width: 30px;
  &:hover,&:focus{
    color: $gray-dark;
    opacity: 1;
  }
  @include iPad{
    width: 60px;
  }
  @include mobile-lg{
    width: 30px;
  }
}